    background: linear-gradient(#607d8b, #455a64);
    font-family: "Roboto", sans-serif;
  } */
  
  form {
    margin: 3em auto;
    max-width: 330px;
    background: #ffffff;
    padding: 40px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
  }
  
  .label-password {
    display: block;
    position: relative;
    text-transform: uppercase;
    font-weight: 700;
    width: 100%;
    color: #727272;
  }
  
  .input-password {
    display: block;
    text-transform: none;
    width: 100%;
    height: 40px;
    border-width: 0 0 1px;
    border-style: solid;
    border-color: #b6b6b6;
    font-weight: 600;
    font-size: 16px;
    color: #212121;
  }
  
  .input-password:focus,
  .input-password:active {
    border-color: #00bcd4;
    outline: 0;
  }
  
  .show-password {
    cursor: pointer;
    position: absolute;
    bottom: 11px;
    height: 16px;
    right: 0;
    background: #727272;
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-weight: 700;
    font-size: 0.8em;
  }
  
  .strength-password {
    position: absolute;
    width: 0%;
    height: 5px;
    bottom: -8px;
    left: 0;
    background: transparent;
    transition: all 300ms ease-in-out;
  }
  
  .strength-password[data-score="null"] {
    width: 0;
    background: red;
  }
  
  .strength-password[data-score="0"] {
    width: 5%;
    background: #f44336;
  }
  
  .strength-password[data-score="1"] {
    width: 33%;
    background: #f44336;
  }
  
  .strength-password[data-score="2"] {
    width: 66%;
    background: #ffeb3b;
  }
  
  .strength-password[data-score="3"] {
    width: 100%;
    background: #4caf50;
  }
  
